export default async function Fetch(url: string, body: BodyInit = null) {
    const headers = new Headers();
    headers.append('pragma', 'no-cache');
    headers.append('cache-control', 'no-cache');

    return fetch(url, {
        method: 'POST',
        body: body,
        headers: headers,
        credentials: "same-origin"
    })
    .then(response => {
        if(!response.ok) throw {name : response.status, message : response.statusText}
        return response;
    });
}

export async function formfetch(url: string, formProperties?: {[index: string]: string}) {
    let body = null;
    
    if(formProperties) {
        body = new FormData();
        Object.entries(formProperties).forEach(([key, value])=>{
            body.append(key, value);
        });
    }

    return internalfetch(url, body);
}

export async function jsonfetch(url: string, data?: any) {
    return internalfetch(url, JSON.stringify(data));
}

async function internalfetch(url: string, body?: any) {
    const headers = new Headers();
    headers.append('pragma', 'no-cache');
    headers.append('cache-control', 'no-cache');

    return fetch(url, {
        method: 'POST',
        body: body,
        headers: headers,
        credentials: "same-origin"
    }).then(response => {
        if(!response.ok) throw {name : response.status, message : response.statusText}
        return response;
    });
}